<template>
  <div id="myClients">
    <div class="yoya">yoya股份有限公司财务报表</div>

    <el-table
      :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
      style="width: 100%"
    >
      <el-table-column type="index" label="序号" width="50"></el-table-column>
              <i class="el-icon-caret-bottom"></i>
      <el-table-column label="名字" prop="names" width="100">

      </el-table-column>

      <el-table-column label="时间" prop="time" width="180">
          
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.time }} </span>
        </template>
      </el-table-column>

      <el-table-column label="城市" prop="city" width="150"></el-table-column>
      <el-table-column label="认购量" prop="buynum" width="100">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.buynum }} 件</span>
        </template>
      </el-table-column>
      <el-table-column label="销售量" prop="salenum" width="100">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.salenum }} 件</span>
        </template>
      </el-table-column>
      <el-table-column label="销售额" prop="money" width="100">
        <template slot-scope="scope">
          <span style="margin-left: 10px">￥ {{ scope.row.money }}</span>
        </template>
      </el-table-column>

      <el-table-column align="right" width="200">
        <template slot="header">
          <input ref="key" v-model="keyword" size="mini" placeholder="输入关键字搜索" />
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </template>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- Form -->
    <el-dialog title="客户信息修改" :visible.sync="dialogFormVisible">
      <el-form :model="form" id="addre">
        <el-form-item label="名字" :label-width="formLabelWidth">
          <el-input v-model="form.names" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="时间" :label-width="formLabelWidth">

          <div class="block">
            <el-date-picker v-model="form.time" type="datetime" placeholder="选择日期时间"></el-date-picker>
          </div>
        </el-form-item>

        <el-form-item label="城市" :label-width="formLabelWidth">
          <el-input v-model="form.city" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="认购量" :label-width="formLabelWidth">
          <el-input v-model="form.buynum" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="销售量" :label-width="formLabelWidth">
          <el-input v-model="form.salenum" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="销售额" :label-width="formLabelWidth">
          <el-input v-model="form.money" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="changeinfo">确 定</el-button>
      </div>
    </el-dialog>

    <template>
      <div class="paginationBox">
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 40]"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="tableData.length"
          ></el-pagination>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      currentPage: 1, //初始页
      pagesize: 10, //    每页的数据
      keyword: "",
      search: "",
      prop: false,
      form: {},
      dialogTableVisible: false,
      dialogFormVisible: false,
      value1: "",
      formLabelWidth: "120px"
    };
  },
  methods: {
      // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
        },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },

    onSubmit() {
      var word = this.$refs.key.value;

      //   console.log( this.keyword);

      this.$axios
        .get("/vue/myclients", {
          params: {
            keyword: word
          }
        })
        .then(res => {
          // console.log(res);
          this.tableData = res.data.result;
          if (this.tableData.length) {
            this.$refs.key.value = "";
          }
        });
    },

    handleEdit(index, row) {
      //   console.log(index, row);
      this.form = row;

      this.dialogFormVisible = true;
    },
    changeinfo() {
      // console.log(111);
      this.dialogFormVisible = false;
      // console.log(this.form);

      this.$axios.post("/vue/updateinfo", this.form).then(res => {
        // console.log(res);
      });
      //
      if (!(this.form.buynum || this.form.buynum || this.form.money) < 0) {
      }
    },
    handleDelete(index, row) {
      //    console.log(row.uid);

      // 请求ajax跟新数据
      this.$axios
        .get("/vue/delmyclient", {
          params: {
            id: row.uid
          }
        })
        .then(res => {
          // console.log(res);
          // console.log(100000000);
        });
      this.tableData.splice(index, 1);
    },
    handleclientlist() {
      this.$axios.get("/vue/myclients").then(res => {
        this.tableData = res.data.result;
        // console.log(res);
      });
    }
  },
  mounted() {},
  created() {
    this.handleclientlist();
    //       this.$axios.get("/vue/myclients").then(res => {
    //   this.tableData = res.data.result;
    //   console.log(res);
    // });
  }
};
</script>

<style lang="scss" scoped>
.yoya {
  font-family: "Hiragino Sans GB";
  color: #000;
  font-weight: bold;
  font-size: 30px;
  margin: 10px;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 100%;
}
</style>