<template>
  <div id="changeinfo">
    <el-form
      :model="form"
      status-icon
      :rules="rules"
      ref="form"
      label-width="100px"
      class="demo-form"
    >
      <el-form-item label="用户名" prop="username">
        <el-col :span="12">
          <el-input placeholder="请输入内容" type="username" v-model="form.username"  autocomplete="off"
           :disabled="true" >
            {{this.clientinfo.username}}
          </el-input>
          <!-- <el-input
            type="username"
            v-model="form.username"
            autocomplete="off"
           
           页面中使用了el-dropdown，但是在这个标签里面没有设置它的子
元素，所以会报错，在el-dropdown中添加el-dropdown-menu标签
就好
          >{{clientinfo.username}}</el-input>-->
        </el-col>
      </el-form-item>

      <el-form-item label="手机号" prop="mobile">
        <el-col :span="12">
          <el-input
            type="mobile"
            placeholder="手机号"
            v-model="form.mobile"

            autocomplete="off"
            :disabled="true"
          >{{this.clientinfo.mobile}}</el-input>
        </el-col>
      </el-form-item>

      <el-form-item label="邮箱" prop="email">
        <el-col :span="12">
          <el-input
            type="email"
            v-model="form.email"
            autocomplete="off"
            ref="email"
          >{{this.clientinfo.email}}</el-input>
        </el-col>
      </el-form-item>

      <el-form-item label="备注" prop="md">
        <el-col :span="12">
          <el-input type="md" v-model="form.md" ref="md" autocomplete="off">{{this.clientinfo.md}}</el-input>
        </el-col>
      </el-form-item>

      <el-form-item>
        <el-col :span="12">
          <el-button type="primary" @click="submitForm('form')">提交</el-button>
          <el-button @click="resetForm('form')">重置</el-button>
        </el-col>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  data() {
    var validateMd = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请添加对我们的评价"));
      } else {
        if (this.form.checkPass !== "") {
          this.$refs.form.validateField("checkMd");
        }
        callback();
      }
    };

    var validateEmail = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("邮箱不能为空"));
      } else {
        if (this.form.checkPass !== "") {
          this.$refs.form.validateField("checkEmail");
        }
        callback();
      }
    };
    return {
      form: {
        username: "",
        mobile: "",
        email: "",
        md: "",
       
      },
      //  readonly: true,
      rules: {
        md: [{ validator: validateMd, trigger: "blur" }],

        email: [{ validator: validateEmail, trigger: "blur" }]
      }
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var md = this.$refs.md.value;
          var email = this.$refs.email.value;
          // console.log(this.$refs.email.value);
          //    将form作为参数传递过去
          this.$axios.post("/vue/changeClientInfo", this.form).then(res => {});
        } else {
          // console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.form = {};
    }
  },
  created() {},
  computed: {
    ...mapState(["clientinfo"])
  },
  mounted() {
    this.form= JSON.parse(sessionStorage.client);
    console.log(this.form);
    
 
  }
};
</script>

<style lang="scss" scoped>
</style>