<template>
  <div id="changepwd">
    <el-form :model="pass" ref="pass" label-width="100px" class="demo-dynamic" el-col="12">
      <el-form-item prop="oldpass" label="原始密码">
        <el-col :span="8">
          <el-input v-model="pass.oldpass" placeholder="请输入您的原始密码" ref="origin"></el-input>
        </el-col>
      </el-form-item>

      <el-form-item prop="newpass"
      
       label="新密码">
        <el-col :span="8">
          <el-input v-model="pass.newpass" 
          placeholder="请输入新密码" show-password></el-input>
        </el-col>
      </el-form-item>

      <el-form-item>
        <el-col :span="6">
          <el-button type="primary" @click="submitForm">提交</el-button>

          <el-button @click="resetForm">重置</el-button>
        </el-col>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      pass: {
        oldpass: "",
        newpass: ""
      },
      clientpass: {}
    };
  },
  computed: {
    ...mapState(["stuinfo"])
  },
  methods: {
    submitForm() {
      //   请求后台，修改密码
   
 
        this.$axios
          .post("/vue/stuchangepwd", {
            oldpass: this.pass.oldpass,
            newpass: this.pass.newpass
          })
          .then(res => {
            if (!res.data.flag) {
              this.$router.push({ name: "login" });
            } else {
              //   this.pass.oldpass=res.data.result;
              this.clientpass = JSON.parse(res.config.data);
              this.pass.oldpass = this.clientpass.newpass;
              this.pass.newpass = "";
            }
          });
      
    },
    resetForm() {
      this.pass.oldpass = "";
      this.pass.newpass = "";
    }
  },
  created() {
     
  },
  mounted() {
   
    // console.log(this.stuinfo);
  }
};
</script>

<style lang="scss" scoped>
.el-input {
  position: relative;
  font-size: 14px;
  display: inline-block;
}
</style>